body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  background-color: #93e646;
}

.ruond {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  background-color: yellow;
}
.ruond::before {
  content: "";
  position: absolute;
  width: 290px;
  height: 290px;
  border-radius: 50%;
  background-image: linear-gradient(transparent 0%, transparent 50%, white 50%, white 100%);
  left: calc(50% - 145px);
  bottom: calc(45% - 145px);
}

.headphones {
  position: absolute;
  width: 430px;
  height: 490px;
  border-radius: 49%;
  border-top: 20px solid #575656;
}

.cups {
  position: absolute;
  width: 430px;
  height: 490px;
  border-radius: 49%;
}
.cups::before {
  content: "";
  position: absolute;
  background-color: #575656;
  width: 80px;
  height: 150px;
  top: calc(63% - 150px);
  left: calc(10% - 80px);
  border-right: 20px solid #3d3d3d;
  border-top-left-radius: 25%;
  border-bottom-left-radius: 25%;
}
.cups::after {
  content: "";
  position: absolute;
  background-color: #575656;
  width: 80px;
  height: 150px;
  top: calc(63% - 150px);
  right: calc(10% - 80px);
  border-left: 20px solid #3d3d3d;
  border-top-right-radius: 25%;
  border-bottom-right-radius: 25%;
}